.range {
    width: 400px;
    height: 20px;
    -webkit-appearance: none;
    background: #f1f1f1;
    outline: none;
    border-radius: 15px;
    overflow: hidden;
    box-shadow: inset 0 0 5px rgba(0, 0, 0, 0.5);
}
.range2 {
    width: 250px;
    height: 20px;
