}

/*-- 
    - Common Classes
-----------------------------------------*/
.fix {
    overflow: hidden;
}

.hidden {
    display: none;
}

.clear {
    clear: both;
}

.section,
.main-wrapper {
    float: left;
    width: 100%;
}

.border-bottom {
    border-bottom: 1px solid #eeeeee;
}

.bg-image {
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
}

#scrollUp {
    background: #f4e0cf;
    width: 30px;
    height: 30px;
    line-height: 30px;
    border-radius: 5px 5px 0px 0px;
    bottom: 0px;
    right: 25px;
    color: #003a4e;
    text-align: center;
    font-size: 25px;
    -webkit-transition: all 0.3s ease-in-out;
    -o-transition: all 0.3s ease-in-out;
    transition: all 0.3s ease-in-out;
}

#scrollUp:hover {
    background: #151515;
    color: #fff;
}

