    }
}

@media only screen and (max-width: 575px) {
    .container {
        max-width: 450px;
    }
}

@media only screen and (max-width: 479px) {
    .container {
        max-width: 300px;
    }
}

.no-gutters {
    margin-left: 0;
    margin-right: 0;
}

.no-gutters > .col,
.no-gutters > [class*="col-"] {
    padding-right: 0;
    padding-left: 0;
